:root {
    --success-color: #56A55C;
    --primary-color: #203C42;
    --danger-color: #C13F37;
    --background-primary-color: #FFF;
    --text-primary-color: #FEFEFF;
    --text-secondary-color: #848C96;
    --font-small-size: 12px;
    --font-medium-size: 14px;
    --font-large-size: 16px;
    --font-contrast-size: 20px;
}

.opti-plugin-page {
    background-color: #FFF;
}

.opti-arrow::after {
    content: ' ';
    border-right-color: #FFF !important;
}

#optimarketing {
    padding: 20px 0;
    margin-left: -20px;
    margin-top: -20px;
}

@keyframes fadeInOut {
    0%,
    100% {
        opacity: 0;
    }

    50% {
        opacity: 1;
    }
}

.fade-in-out {
    animation: fadeInOut 2s ease infinite;
}